import React from 'react';
import PropTypes from 'prop-types';

const RowFlex = ({children, cls, ...rest}) => (
  <div className={cls} {...rest}>
    {children}
  </div>
);

RowFlex.propTypes = {
  cls: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.string
  ])
};

export default RowFlex;