import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import {
  SearchToggle,
  NavTrigger
} from './../../components';
import {
  StyledNav,
  StyledNavList
} from './styled';

const ListItem = ({txt}) => <li><a href="#">{txt}</a></li>;

class Nav extends PureComponent {
  render () {
    return (
      <StyledNav>
        <NavTrigger onClick={e => console.log(e)} />
        <StyledNavList>
          {
            ['News', 'Opinion', 'Meanwhile', 'Arts & Life', 'Podcasts']
              .map((v, i) => <ListItem key={i} txt={v} />)
          }
        </StyledNavList>
        <SearchToggle />
      </StyledNav>
    );
  }
}

// Nav.propTypes = {};
ListItem.propTypes = {
  txt: PropTypes.string
};

export default Nav;